export const AddRolesPage: React.FC<IRolesPage.IProps> = (props: IRolesPage.IProps): JSX.Element => {
	const classes = useStyles();
	const [loading, setLoading] = useState(false);
	const [activeItem, setActiveItem] = useState('Policies');
	const [isModalConfirmExitOpen, setIsModalConfirmExitOpen] = useState(false);
	const [isModalConfirmSaveOpen, setIsModalConfirmSaveOpen] = useState(false);
	const [isModalSaveSuccessOpen, setIsmodalSaveSuccessOpen] = useState(false);
	const [moduleName, setModuleName] = useState('User Management');
	const [rbacList, setRbacList] = useState([]);
	const [breadCrumbs, setBreadCrumbs] = useState<any[]>([]);
	const [rbacCount, setRbacCount] = useState(0);
	const [roleId, setRoleId] = useState('');
	const [policyData, setPolicyData] = useState<any>({})
	const [isRedirect, setIsRedirect] = useState<boolean>(false);
	const [redirectPath, setRedirectPath] = useState<string>('')
	const [isSaveDisabled, setIsSaveDisabled] = useState<boolean>(true);
	const [rbacState, setRbacState] = useState<any>({
		filter: [],
		order: '',
		limit: 40,
		skip: 0,
		count: 0,
		currentPage: 0
	})
	const [typingTimeout, setTypingTimeout] = useState<any>(0);

	const sideBarItems = [
		{
			name: 'Policies',
		},
	];

	React.useEffect(() => {
		console.log(props)
		if (props) {
			if (props.location) {
				if (props.location.state) {
					if (props.location.state.from) {
						setModuleName(props.location.state.from)
						handleGetRbacCount(props.location.state.from)
					}
				} else {
					handleGetRbacCount(moduleName)
				}
			}
		}
		var _crumbs: any[] = [
			{
				name: "ROLES",
				link: "/system-admin/users/roles"
			},
			{
				name: "ADD A ROLE",
			}
		];
		setBreadCrumbs(_crumbs);
		handGetRbacList(
			rbacState.filter,
			rbacState.order,
			rbacState.limit,
			rbacState.skip,
			rbacState.currentPage,
		);
	}, []);

	const handleGetRbacCount = (name: string) => {
		getRbacCount(name).then(data => {
			setRoleId(generateRoleId(name, data.count + 1));
			setRbacCount(data.count + 1);
		}).catch(err => {
			console.log(err);
			alert(err);
		})
	}


	// value={addData.expiryDate !== '' ? addData.expiryDate : inputtedData.expiry_date}
	// value={(addData.effectivityDate && addData.effectivityDate !== "" && inputtedData.effectivityDate === null) ? addData.effectivityDate
	//     : (inputtedData.effectivityDate) ? inputtedData.effectivityDate
	//         : null}
	const handGetRbacList = (
		filter: any,
		sort: any,
		limit: number,
		skip: number,
		currentPage: number,
		resParam?: any,
		search?: string,
	) => {

		if (typingTimeout) {
			clearTimeout(typingTimeout)
		}

		let _filter: any = {};
		let _order = sort ? `${sort[0].columnName} ${sort[0].direction}` : '';

		if (filter.length > 0) {
			filter.map((item: any) => {
				_filter[item.columnName] = {
					like: `^${item.value}.*`,
					options: "i"
				}
			})
		} else {
			_filter = {
				module: {
					like: "^.*",
					options: "i"
				}
			}
		}

		let params = {
			limit: limit,
			skip: skip,
			order: _order,
			where: _filter
		}

		setTypingTimeout(setTimeout(() => {
			setLoading(true)
			getRbacList(params, search ? search : '').then(response => {
				let res = response.body;
				setRbacState({
					currentPage: currentPage,
					filter: filter,
					order: sort,
					limit: limit,
					skip: skip,
					count: response.count
				})

				resParam && res.length > 0 && res.map((item: any) => {
					resParam.rbac && resParam.rbac.length > 0 && resParam.rbac.map((policy: any) => {
						if (item._id === policy._id) {
							item['checked'] = true;
						}
					})
				})

				setRbacList(res)
				setLoading(false)
			}).catch(err => {
				console.log(err)
				setLoading(false)
				alert(err);
			})
		}, filter.length > 0 || search ? 2000 : 0))
	}

	// const handleGetGroupsAndUsers = () => {

	// }

	const activeSideBarItem = (item) => {
		console.log(item)
		setActiveItem(item);
	}

	const handleModalConfirmationClose = () => {
		setIsModalConfirmSaveOpen(false);
		setIsModalConfirmExitOpen(false);
	}

	const generateRoleId = (moduleName: string, count: number) => {
		let roleId = '';

		switch (moduleName) {
			case 'User Management':
				roleId = `RID0${count}`
				break;
			case 'Membership':
				roleId = `MRID0${count}`
				break;
			case 'Underwriting':
				roleId = `URID0${count}`
				break;
			case 'Customer Care':
				roleId = `CRID0${count}`
				break;
			case 'Claims':
				roleId = `CLRID0${count}`
				break;
			default:
				roleId = `RID0${count}`
				break;
		}
		return roleId;
	}

	const handleChangeModuleName = (moduleName) => {
		setRoleId(generateRoleId(moduleName, rbacCount));
	}


	const handleOnSave = () => {
		let data = policyData;
		data.rbac && data.rbac.map((rbac: any) => {
			if (rbac.checked) {
				delete rbac.checked
			}
		})
		setLoading(true)
		createRoleRbac(data).then((response: any) => {
			setIsmodalSaveSuccessOpen(true);
			setIsModalConfirmSaveOpen(false);
			setRedirectPath('/system-admin/roles/' + response._id);
			setIsRedirect(true)
			setLoading(false);
		}).catch(err => {
			setLoading(false)
			alert(err);
		})
	}

	const handleRemoveAllChanges = () => {
		setIsModalConfirmExitOpen(false)
		setRedirectPath('/system-admin/users')
		setIsRedirect(true);
	}

	const handleUpdateChange = (data: any, isValid: boolean) => {
		setPolicyData(data);
		setIsSaveDisabled(!isValid)
	}

	return (
		<>
			{
				isRedirect &&
				<Redirect
					to={{
						pathname: redirectPath,
					}}
				/>
			}
			<SubHeader
				headingTitle={breadCrumbs[1] ? breadCrumbs[1].name : ''}
				breadCrumbsArray={breadCrumbs}
				layoutType={'layout_1'}
			></SubHeader>
			<div className={classes.main}>
				{loading ?
					(<LoadingIndicator />) : null
				}
				<Grid container xs={12}>
					<Grid item xs={3} className={classes.sideBarContainer}>
						<SideMenu
							items={sideBarItems}
							activeItems={activeSideBarItem}
						></SideMenu>
					</Grid>
					<Grid item xs={9} className={classes.contentContainer}>
						{
							activeItem == 'Policies' &&
							<Policies
								rbacList={rbacList}
								moduleName={moduleName}
								roleId={roleId}
								classes={classes}
								rbacState={rbacState}
								inputtedData={policyData}
								onSearch={(search: string) => {
									handGetRbacList(
										rbacState.filter,
										rbacState.order,
										rbacState.limit,
										0,
										rbacState.page,
										policyData,
										search
									)
								}}
								onNavigateSideMenu={(name: string) => {
									activeSideBarItem(name)
								}}
								onChangeModuleName={(name: string) => {
									handleChangeModuleName(name)
								}}
								onUpdateChange={(data: any, isValid: boolean) => {
									handleUpdateChange(data, isValid);
								}}
								onPageChange={(page: number) => {
									let skip = page * rbacState.limit
									handGetRbacList(
										rbacState.filter,
										rbacState.order,
										rbacState.limit,
										skip,
										page,
										policyData
									)
								}}
								onPageSizeChange={(size: number) => {
									handGetRbacList(
										rbacState.filter,
										rbacState.order,
										size,
										rbacState.skip,
										rbacState.currentPage,
										policyData
									)
								}}
								onFilterChange={(filter: any) => {
									handGetRbacList(
										filter,
										rbacState.order,
										rbacState.limit,
										0,
										0,
										policyData
									)
								}}
								onSortChange={(sort: any) => {
									handGetRbacList(
										rbacState.filter,
										sort,
										rbacState.limit,
										rbacState.skip,
										rbacState.currentPage,
										policyData
									)
								}}
							></Policies>
						}
					</Grid>
				</Grid>
			</div>
			<ModalComponent
				isModalOpen={isModalConfirmSaveOpen}
				onClose={handleModalConfirmationClose}
				message={'Save all changes made?'}
				title={'Confirmation'}
				buttons={
					<Grid container justify="center" alignItems="center">
						<Grid item xs={6}>
							<Button onClick={() => { setIsModalConfirmSaveOpen(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
						</Grid>
						<Grid item xs={6}>
							<Button
								className={classes.button}
								variant={'contained'}
								color={'primary'}
								onClick={() => handleOnSave()}
							>Save</Button>
						</Grid>
					</Grid>
				}
			/>
			<ModalComponent
				isModalOpen={isModalConfirmExitOpen}
				onClose={handleModalConfirmationClose}
				message={'Exit and remove all changes made?'}
				title={'Confirmation'}
				buttons={
					<Grid container justify="center" alignItems="center">
						<Grid item xs={6}>
							<Button onClick={() => { setIsModalConfirmExitOpen(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
						</Grid>
						<Grid item xs={6}>
							<Button
								className={classes.button}
								variant={'contained'}
								color={'primary'}
								onClick={() => {
									handleRemoveAllChanges()
								}}
							>Continue</Button>
						</Grid>
					</Grid>
				}
			/>
			<ModalComponent
				isModalOpen={isModalSaveSuccessOpen}
				onClose={() => { setIsmodalSaveSuccessOpen(false) }}
				message={'Changes has been saved.'}
				title={'Save Success'}
				buttons={
					<Grid container justify="center" alignItems="center">
						<Grid item xs={6}>
							<Button
								className={classes.button}
								variant={'contained'}
								color={'primary'}
								onClick={() => { setIsmodalSaveSuccessOpen(false) }}
							>Okay</Button>
						</Grid>
					</Grid>
				}
			/>
			{
				activeItem === 'Policies' ?
					<FloatingFooter>
						<Grid container spacing={1} justify={'flex-end'} style={{ textAlign: 'right' }}>
							<Grid item>
								<Button
									className={classes.button}
									variant={'contained'}
									color={'primary'}
									style={{ backgroundColor: '#3C394A' }}
									onClick={() => setIsModalConfirmExitOpen(true)}
								>Cancel</Button>
							</Grid>
							<Grid item>
								<Button
									className={classes.button}
									variant={'contained'}
									color={'primary'}
									onClick={() => setIsModalConfirmSaveOpen(true)}
									disabled={isSaveDisabled}
								>Save Changes</Button>
							</Grid>
						</Grid>
					</FloatingFooter>
					: ""
			}
		</>
	);
}

const mapStateToProps = (state: Store) => state.roles;

const mapDispatchToProps = (dispatch: Dispatch) => ({
	Map: bindActionCreators(RolesActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
