import * as React from 'react';
import {
    Grid,
    // FormControl, 
    InputLabel,
    Typography,
    Link,
    Button,
    Checkbox
    // TextareaAutosize,
} from '@material-ui/core';
import { ReactTable } from 'Components/UI/TableComponent/ReactTable';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { BasicTextField, BasicSelect } from 'Components/UI';
import { KeyboardDatePicker } from '@material-ui/pickers';
import { ModalComponent } from 'Components/UI/ModalComponent/ModalComponent';
import moment from 'moment';
import { RolesRbacData } from '../IRolesPage';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExclamationCircle } from '@fortawesome/free-solid-svg-icons';
export interface IPoliciesProps {
    classes: any,
    moduleName?: string,
    rbacList: any,
    rbacState: any,
    roleId: string,
    onPageChange: (page) => void,
    onFilterChange: (filter) => void,
    onSortChange: (sort) => void,
    onPageSizeChange: (size) => void,
    onSearch: (search) => void,
    onChangeModuleName: (name) => void,
    onUpdateChange: (data, isValid) => void,
    onNavigateSideMenu?: (name) => void,
    inputtedData: any
}

export const Policies: React.FC<IPoliciesProps> = (props: IPoliciesProps): JSX.Element => {
    const {
        classes,
        moduleName,
        rbacList,
        onChangeModuleName,
        roleId,
        onUpdateChange,
        onPageChange,
        onFilterChange,
        onSortChange,
        onPageSizeChange,
        onSearch,
        rbacState,
        inputtedData
    } = props;

    const [isAddedModalRole, setIsAddedModalRole] = React.useState(false)
    const [moduleValue, setModuleValue] = React.useState(0);
    const [isModuleModalOpen, setIsModuleModalOpen] = React.useState(false);
    const [moduleLabel, setModuleLabel] = React.useState<any>(null);
    const [isModalConfirmChangeModule, setIsModalConfirmChangeModule] = React.useState(false)
    const [newRbacList, setNewRbacList] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [defaultFilters, setDefaultFilters] = React.useState([]);
    const [isModalDeleteSuccessOpen, setIsModalDeleteSuccessOpen] = React.useState(false)
    const [isModalCannotEditOpen, setIsModalCannotEditOpen] = React.useState(false)

    const [addData, setAddData] = React.useState<any>({
        description: '',
        roleName: '',
        isProcessMaker: false,
        effectivityDate: new Date(),
        expiryDate: '',
    })

    const [errorFields, setErrorFields] = React.useState<any>({
        description: {
            required: 'Description is a required field.',
            isError: false,
        },
        roleName: {
            required: 'Role name is a required field.',
            isError: false
        },
        effectivityDate: {
            required: 'Effectivity date is a required field.',
            minDate: 'Effectivity date cannot be higher or equal than expiry date.',
            isError: false,
        },
        expiryDate: {
            minDate: 'Expiry date cannot be lower than effectivity date.',
            isError: false,
        }
    })

    const moduleItems = [
        {
            name: 'User Management',
            value: '0'
        },
        {
            name: 'Membership',
            value: '1'
        },
        {
            name: 'Underwriting',
            value: '2'
        },
        {
            name: 'Customer Care',
            value: '3'
        },
        {
            name: 'Claims',
            value: '4',
        },
    ]

    const columns: any = [
        {
            name: 'operation',
            title: 'Object and Operations',
        },
        {
            name: 'policy',
            title: 'Policies',
        },
        {
            name: 'policy_id',
            title: 'Policy ID',
        },
        {
            name: 'name',
            title: 'Name'
        }
    ]

    const handleCannotEditRoleModalClose = () => {
        setIsModalCannotEditOpen(false)
    }

    const handleDeleteSuccessRoleModalClose = () => {
        setIsModalDeleteSuccessOpen(false)
    }

    const handleRemoveRoleModalClose = () => {

    }

    const handleContinueRole = () => {
        let moduleName = moduleItems[Number(moduleValue)].name;
        setIsModalConfirmChangeModule(false);
        setModuleLabel(moduleName);
        onChangeModuleName(moduleName);
    }

    const handleOnCheckRow = (row) => {
        setLoading(true);

        let rbac = rbacList;
        if (newRbacList.length === 0) {
            setNewRbacList(rbacList);
        } else {
            rbac = newRbacList;
        }
        if (rbac.length > 0) {
            rbac.map((data: any) => {
                if (data._id === row._id) {
                    data['checked'] = !data['checked'];
                }
            })
            setNewRbacList(rbac);
        }
        setLoading(false)
    }

    const handleOnCheckGroup = (group) => {
        setLoading(true);
        let rbac = rbacList;
        let countChecked = 0;

        if (newRbacList.length === 0) {
            setNewRbacList(rbacList)
        }

        if (rbac.length > 0) {
            rbac.map((data: any) => {
                if (data.name == group && data.checked === true) {
                    countChecked++;
                }
            })
        }
        if (countChecked > 0) {
            rbac.map((data: any) => {
                if (data.name == group) {
                    data['checked'] = false;
                }
            })
        } else {
            rbac.map((data: any) => {
                if (data.name == group) {
                    data['checked'] = true;
                }
            })
        }
        setNewRbacList(rbac);
        setTimeout(() => {
            setLoading(false);
        }, 500)
    }

    const handleUpdateCheck = (_data: any, isValid: boolean) => {
        var rbac: any[] = [];
        const _rbacList = newRbacList.length > 0 ? newRbacList : rbacList;

        rbac = _rbacList.filter((data: any) => {
            return data.checked;
        })

        var data: RolesRbacData = {
            module: moduleLabel !== null ? moduleLabel : moduleName,
            name: _data.roleName,
            role_id: roleId,
            effectivity_date: moment(_data.effectivityDate !== '' ? _data.effectivityDate : new Date()).toISOString(),
            description: _data.description,
            status: 'active',
            date_last_update: moment().toISOString(),
            is_processmaker: _data.isProcessMaker,
            rbac: rbac,
        }

        if (_data.expiryDate !== '') {
            data['expiry_date'] = moment(_data.expiryDate).toISOString();
        }

        console.log(rbac.length > 0, isValid, 'rbac');
        if (isValid && rbac.length > 0) {
            onUpdateChange(data, true);
        } else {
            onUpdateChange(data, false);
        }
    }

    const isValidData = (data: any) => {
        let isValid = false;
        for (var key in errorFields) {
            if (errorFields[key].isError !== true) {
                isValid = true
            } else {
                handleUpdateCheck(data, false)
                return false
            }
        }

        for (var key in data) {
            console.log(data[key])
            if (data[key] === '' && key !== 'isProcessMaker' && key !== 'expiryDate') {
                handleUpdateCheck(data, false)
                return false
            } else {
                isValid = true
            }
        }
        handleUpdateCheck(data, true)
        return isValid;
    }

    const handleOnFilterChange = (filter) => {
        setDefaultFilters(filter);
        onFilterChange(filter)
    }

    console.log(`added expiry date = ${addData.expiryDate}`)
    console.log(`inputted expiry date = ${inputtedData.expiry_date}`)


    return (
        <>
            <Grid container xs={12} spacing={4}>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel} htmlFor="role_id">MODULE</InputLabel>
                    <Typography><b>{moduleLabel !== null ? moduleLabel : moduleName}</b></Typography>
                    <Link onClick={() => { setIsModuleModalOpen(true) }}>Change Module</Link>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel}>ROLE ID</InputLabel>
                    <Typography><b>{roleId}</b></Typography>
                </Grid>
                <Grid item xs={4}>
                    <InputLabel className={classes.inputLabel}>DESCRIPTION *</InputLabel>
                    <BasicTextField
                        value={addData.description !== '' ? addData.description : inputtedData.description}
                        error={errorFields.description.isError}
                        onChange={(e: any) => {
                            setAddData({
                                ...addData,
                                description: e.target.value
                            })
                            if (e.target.value === '' || null) {
                                setErrorFields({
                                    ...errorFields,
                                    description: {
                                        required: errorFields.description.required,
                                        isError: true
                                    }
                                })
                            } else {
                                setErrorFields({
                                    ...errorFields,
                                    description: {
                                        required: errorFields.description.required,
                                        isError: false
                                    }
                                })
                            }
                            isValidData({
                                ...addData,
                                description: e.target.value
                            })
                        }}
                    />
                    {
                        errorFields.description.isError ?
                            <div className={classes.errorMessageContainer}>
                                <span className={classes.errorMessage}>{errorFields.description.required}</span>
                                <FontAwesomeIcon
                                    className={classes.errorMessageIcon}
                                    icon={faExclamationCircle}
                                />
                            </div>
                            : null
                    }
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel}>ROLE NAME *</InputLabel>
                    <BasicTextField
                        value={addData.roleName ? addData.roleName : inputtedData.name}
                        error={errorFields.roleName.isError}
                        onChange={(e: any) => {
                            setAddData({
                                ...addData,
                                roleName: e.target.value
                            })
                            if (e.target.value === '' || null) {
                                setErrorFields({
                                    ...errorFields,
                                    roleName: {
                                        required: errorFields.roleName.required,
                                        isError: true
                                    }
                                })
                            } else {
                                setErrorFields({
                                    ...errorFields,
                                    roleName: {
                                        required: errorFields.roleName.required,
                                        isError: false
                                    }
                                })
                            }
                            isValidData({
                                ...addData,
                                roleName: e.target.value
                            })
                        }}
                    />
                    {
                        errorFields.roleName.isError ?
                            <div className={classes.errorMessageContainer}>
                                <span className={classes.errorMessage}>{errorFields.roleName.required}</span>
                                <FontAwesomeIcon
                                    className={classes.errorMessageIcon}
                                    icon={faExclamationCircle}
                                />
                            </div>
                            : null
                    }
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel}>IS A PROCESSMAKER PROCESS MAKER ROLE</InputLabel>
                    <Checkbox
                        checked={addData.isProcessMaker ? addData.isProcessMaker : inputtedData.is_processmaker}
                        onChange={(e: any) => {
                            setAddData({
                                ...addData,
                                isProcessMaker: e.target.checked
                            })
                            isValidData({
                                ...addData,
                                isProcessMaker: e.target.checked
                            })
                        }}
                    />
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel}>EFFECTIVITY DATE *</InputLabel>
                    <KeyboardDatePicker
                        error={errorFields.effectivityDate.isError}
                        value={addData.effectivityDate !== '' ? addData.effectivityDate : new Date()}
                        format={'MMMM DD, YYYY'}
                        variant='dialog'
                        invalidDateMessage=" "
                        style={{
                            width: "100%",
                            marginTop: 10,
                            marginBottom: 5
                        }}
                        onChange={(e: any) => {
                            setAddData({
                                ...addData,
                                effectivityDate: e
                            })
                            if (e === null || '') {
                                setErrorFields({
                                    ...errorFields,
                                    effectivityDate: {
                                        required: errorFields.effectivityDate.required,
                                        isError: true,
                                    }
                                })
                            } else if (moment(e) >= moment(addData.expiryDate)) {
                                setErrorFields({
                                    ...errorFields,
                                    effectivityDate: {
                                        minDate: errorFields.effectivityDate.minDate,
                                        isError: true,
                                    }
                                })
                            } else {
                                setErrorFields({
                                    ...errorFields,
                                    effectivityDate: {
                                        minDate: errorFields.effectivityDate.minDate,
                                        required: errorFields.effectivityDate.required,
                                        isError: false,
                                    }
                                })
                            }
                            isValidData({
                                ...addData,
                                effectivityDate: e
                            })
                        }}
                        inputVariant={'outlined'}
                    />
                    {
                        errorFields.effectivityDate.isError ?
                            <div className={classes.errorMessageContainer}>
                                <span className={classes.errorMessage}>{errorFields.effectivityDate.required ? errorFields.effectivityDate.required : errorFields.effectivityDate.minDate}</span>
                                <FontAwesomeIcon
                                    className={classes.errorMessageIcon}
                                    icon={faExclamationCircle}
                                />
                            </div>
                            : null
                    }
                </Grid>
                <Grid item xs={6}>
                    <InputLabel className={classes.inputLabel}>EXPIRY DATE (If applicable)</InputLabel>
                    <KeyboardDatePicker
                        minDate={moment(addData.effectivityDate).add(1, 'days')}
                        minDateMessage={""}
                        value={(addData.expiryDate && addData.expiryDate !== "") ? addData.expiryDate
                            : (inputtedData.expiry_date && inputtedData.expiry_date !== null) ? inputtedData.expiry_date
                                : (addData.expiryDate !== "" && inputtedData.expiry_date !== "") ? inputtedData.expiry_date
                                    : null}

                        style={{ width: "100%", marginTop: 10 }}
                        format={'MMMM DD, YYYY'}
                        invalidDateMessage=""
                        variant='dialog'
                        onChange={(e: any) => {
                            setAddData({
                                ...addData,
                                expiryDate: e
                            })
                            if (moment(addData.effectivityDate) > moment(e)) {
                                setErrorFields({
                                    expiryDate: {
                                        minDate: errorFields.expiryDate.minDate,
                                        isError: true
                                    }
                                })
                            } else {
                                setErrorFields({
                                    ...errorFields,
                                    expiryDate: {
                                        minDate: errorFields.expiryDate.minDate,
                                        isError: false,
                                    }
                                })
                            }
                            isValidData({
                                ...addData,
                                expiryDate: e
                            })
                        }}
                        inputVariant={'outlined'}
                    />
                    {
                        errorFields.expiryDate.isError ?
                            <div className={classes.errorMessageContainer}>
                                <span className={classes.errorMessage}>{errorFields.expiryDate.minDate}</span>
                                <FontAwesomeIcon
                                    className={classes.errorMessageIcon}
                                    icon={faExclamationCircle}
                                />
                            </div>
                            : null
                    }
                </Grid>
                <Grid item xs={12}>

                    <ReactSearch
                        onSearch={onSearch}
                        onChange={onSearch}
                        placeholder={'Search policies'}
                    />
                    {
                        <ReactTable
                            rows={rbacList}
                            columns={columns}
                            enableGrouping={true}
                            groupColumnName={'name'}
                            viewOnly={false}
                            enableOpenGroupTotalChecked={false}
                            message={'There are no policies to show yet.'}
                            enableSelection={true}
                            loading={loading}
                            onCheckRow={(row) => {
                                handleOnCheckRow(row)
                                isValidData(addData)
                            }}
                            currentPage={rbacState.currentPage}
                            pageSize={rbacState.limit}
                            defaultFilters={defaultFilters}
                            onFilterChange={handleOnFilterChange}
                            onSortingChange={onSortChange}
                            onSetPageSize={onPageSizeChange}
                            onPageChange={onPageChange}
                            totalCount={rbacState.count ? rbacState.count : 0}
                            onSelectGroup={(group) => {
                                handleOnCheckGroup(group)
                                isValidData(addData)
                            }}
                        />
                    }
                </Grid>
            </Grid>
            <ModalComponent
                isModalOpen={isAddedModalRole}
                onClose={handleRemoveRoleModalClose}
                message={'New Role has been added successfully.'}
                title={'Added New Role'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button onClick={() => { setIsAddedModalRole(false) }} className={classes.button} variant={'contained'} color={'primary'}>Okay</Button>
                        </Grid>
                    </Grid>
                }
            />
            <ModalComponent
                isModalOpen={isModuleModalOpen}
                onClose={() => {
                    setIsModuleModalOpen(false)
                }}
                message={'Select a module for the role you will create:'}
                title={'Choose a Module'}
                buttons={
                    <Grid container justify="center" alignItems="center" style={{ textAlign: 'center' }}>
                        <Grid item xs={6}>
                            <Button
                                onClick={() => {
                                    setIsModuleModalOpen(false)
                                }}
                                variant={'outlined'}
                                color={'secondary'}
                                className={classes.button}
                            >Cancel</Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button variant={'contained'} color={'primary'}
                                className={classes.button}
                                onClick={() => {
                                    setIsModalConfirmChangeModule(true)
                                    setIsModuleModalOpen(false)
                                }}>Continue</Button>
                        </Grid>
                    </Grid>
                }
            >
                <div style={{ width: '90%', margin: 'auto' }}>
                    <BasicSelect
                        menuItems={moduleItems}
                        width={'100%'}
                        value={moduleValue}
                        onChange={(e: any) => {
                            setModuleValue(e.target.value)
                        }}
                    />
                </div>
            </ModalComponent>
            <ModalComponent
                isModalOpen={isModalConfirmChangeModule}
                onClose={() => {
                    setIsModalConfirmChangeModule(false);
                }}
                message={'All progress made will be lost. Continue to change the module?'}
                title={'Confirm Change Module'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button onClick={() => { setIsModalConfirmChangeModule(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button className={classes.button} variant={'contained'} color={'primary'} onClick={() => handleContinueRole()}>Continue</Button>
                        </Grid>
                    </Grid>
                }
            />

            <ModalComponent
                isModalOpen={isModalDeleteSuccessOpen}
                onClose={handleDeleteSuccessRoleModalClose}
                message={'New role has been deleted.'}
                title={'Delete Success'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item >
                            <Button
                                onClick={() => {
                                    setIsModalDeleteSuccessOpen(false)
                                }}
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}>
                                Okay
                            </Button>
                        </Grid>
                    </Grid>
                }
            />


            <ModalComponent
                isModalOpen={isModalCannotEditOpen}
                onClose={handleCannotEditRoleModalClose}
                message={'You do not have an assigned role to your account to perform this action.'}
                title={'Cannot Edit Role'}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item>
                            <Button
                                onClick={() => {
                                    setIsModalCannotEditOpen(false)
                                }}
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}>
                                Okay
                            </Button>
                        </Grid>
                    </Grid>
                }
            />
        </>
    )
}